@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
body{
  display:flex;
  justify-content:center;
  align-items:center;
  width:100%;
  height:100vh;
  -ms-scroll-chaining:none;
      overscroll-behavior:none;
}
#button-wrapper{
    display:flex;
    justify-content:center;
    align-items:center;
    width:80px;
    height:75px;
    font-size:2em;
    border-radius:25%;
    text-align:center;
    cursor:pointer;
    position:relative;
    outline:none;
    border:none;
    margin:3px 4px;
    color:white;
    font-family:"Share Tech Mono", "monospace";
    border:1.5px solid #666;
}
#button-wrapper:hover{
    position:relative;
    border:.1px solid white;
    transform:scale(1.02);
    color:black;
}
#formula-wrapper{
    background-color:#555;
    width:100%;
    height:55px;
    display:flex;
    align-items:center;
    justify-content:flex-end;
    font-size:1.5em;
    font-family:"Orbitron";
    color:orange;
    border-bottom:2px solid #111;
    border-radius:5px;
    border-top:1.5px solid #666;
    border-right:1.5px solid #666;
    border-left:1.5px solid #666;
}
#output-wrapper{
    width:100%;
    height:75px;
    background-color:#555 ;
    margin-bottom:1em;
    text-align:right;
    display:flex;
    align-items:center;
    justify-content:flex-end;
    font-size:2em;
    font-family:"Orbitron";
    border-radius:5px;
    border-right:1.5px solid #666;
    border-bottom:1.5px solid #666;
    border-left:1.5px solid #666;
}
#calculator{
    background-color:#111;
    color:white;
    margin:auto;
    max-width:450px;
    width:100%;
    height:590px;
    padding:2em;
    box-shadow:5px 5px 8px 3px #777;
    border-radius:3%;
    display:flex;
    flex-direction:column;
}
#buttons{
    width:100%;
    height:100%;
    padding:10px 0;
    display:flex;
    flex-wrap:wrap;
    justify-content:center;
    margin-top:1em;
    position:relative;
}
#switches{
    font-family:"Share Tech Mono";
    display:flex;
    font-size:1.3em;
}
.switch{
    display:flex;
    align-items:center;
    margin-right:15px;
    
}
.switch-wrapper{
    width:50px;
    height:25px;
    background-color:#707070;
    margin-left:5px;
    border-radius:7px;
}
.button{
    width:49%;
    height:99%;
    background-color:white;
    border-radius:40%;
}

